import React from "react";

interface VitalsCardProps {
  label: string;
  value: string | number;
  unit?: string;
  icon: React.ReactElement;
  bgClass?: string; // Add this line
}

export default function VitalsCard({ label, value, unit, icon, bgClass }: VitalsCardProps) {
  return (
    <div className={`rounded-2xl p-5 flex items-center gap-4 shadow-md ${bgClass || "bg-white text-black"}`}>
      <div className="text-3xl">{icon}</div>
      <div>
        <p className="text-sm opacity-80">{label}</p>
        <p className="text-xl font-bold">{value} {unit}</p>
      </div>
    </div>
  );
}
