"use client";

import { useEffect, useState } from "react";
import { FaHome, FaHistory, FaCalendarAlt, FaUser, FaCog, FaSignOutAlt, FaHeartbeat, FaTint, FaThermometerHalf, FaLungs, FaWind, FaTemperatureHigh, FaBurn } from "react-icons/fa";
import VitalsCard from "../components/dashboard/VitalsCard";
import HeartRateChart from "/components/dashboard/HeartRateChart";
import { useRouter } from "next/navigation";

export default function Dashboard() {
  const router = useRouter();
  const [currentBpm, setCurrentBpm] = useState(72);
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [userName] = useState("John Doe");
  const [respirationRate] = useState(18);
  const [skinTemp] = useState(97.8);
  const [sweatLevel] = useState("Normal");
  const [pulseRate] = useState(76);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentBpm(prev => {
        const newBpm = prev + (Math.random() > 0.5 ? 1 : -1) * Math.floor(Math.random() * 5);
        return Math.max(60, Math.min(110, newBpm));
      });
      setBpmData(prev => [...prev.slice(-9), { time: new Date().toLocaleTimeString().slice(0, 5), bpm: currentBpm }]);
    }, 3000);
    return () => clearInterval(interval);
  }, [currentBpm]);

  const heartRisk = currentBpm > 120 ? "‚ö†Ô∏è High Heart Rate" : "‚úÖ Normal Heart Rate";
  const feverRisk = parseFloat("98.6") > 100 ? "ü§í Fever Detected" : "‚úÖ Normal Body Temperature";
  const oxygenStatus = 98 < 95 ? "‚ö†Ô∏è Low Oxygen Level" : "‚úÖ Oxygen Normal";
  const respirationStatus = respirationRate > 25 ? "‚ö†Ô∏è High Respiration Rate" : respirationRate < 12 ? "‚ö†Ô∏è Low Respiration Rate" : "‚úÖ Normal Respiration";
  const skinTempStatus = skinTemp > 100 ? "‚ö†Ô∏è High Skin Temperature" : skinTemp < 95 ? "‚ö†Ô∏è Low Skin Temperature" : "‚úÖ Normal Skin Temperature";
  const sweatStatus = sweatLevel === "High" ? "‚ö†Ô∏è Excessive Sweating" : sweatLevel === "Low" ? "‚ö†Ô∏è Low Sweat Level" : "‚úÖ Normal Sweat Level";

  return (
    <div className="min-h-screen bg-gradient-to-r from-blue-500 to-purple-600 flex flex-col px-6 py-6">
      {/* Header */}
      <div className="rounded-2xl p-5 flex justify-between items-center shadow-lg text-white bg-gradient-to-r from-blue-500 to-purple-600">
        <div>
          <h1 className="text-2xl font-bold">BeatAware</h1>
          <p className="mt-1 text-sm italic opacity-90">‚ÄúListen to your heart, BeatAware helps you care.‚Äù üíô</p>
        </div>
        <div className="flex items-center gap-4">
          <div className="w-10 h-10 flex items-center justify-center bg-white text-blue-600 font-bold rounded-full shadow-md">{userName.charAt(0)}</div>
          <span className="font-semibold text-lg">{userName}</span>
          <button className="flex items-center gap-1 bg-white/20 hover:bg-white/30 px-3 py-1.5 rounded-xl text-sm shadow transition">
            <FaCog /> Settings
          </button>
          <button className="flex items-center gap-1 bg-red-500 hover:bg-red-600 px-3 py-1.5 rounded-xl text-sm shadow transition" onClick={() => router.push("/")}>
            <FaSignOutAlt /> Sign Out
          </button>
        </div>
      </div>

      {/* Vitals Cards */}
      <div className="grid grid-cols-2 gap-6 mt-6">
        <VitalsCard label="Heart Rate" value={currentBpm} unit="bpm" icon={<FaHeartbeat />} />
        <VitalsCard label="Blood Pressure" value="120/80" unit="mmHg" icon={<FaTint />} />
        <VitalsCard label="Body Temp." value="98.6" unit="¬∞F" icon={<FaThermometerHalf />} />
        <VitalsCard label="Oxygen Saturation" value="98" unit="%" icon={<FaLungs />} />
        <VitalsCard label="Respiration Rate" value={respirationRate} unit="bpm" icon={<FaWind />} />
        <VitalsCard label="Skin Temp." value={skinTemp} unit="¬∞F" icon={<FaTemperatureHigh />} />
        <VitalsCard label="Pulse Rate" value={pulseRate} unit="bpm" icon={<FaHeartbeat />} />
        <VitalsCard label="Sweat Level" value={sweatLevel} icon={<FaBurn />} />
      </div>

      {/* Heart Rate Chart */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6">
        <h3 className="text-lg font-semibold mb-3">Heart Rate Trend</h3>
        <HeartRateChart data={bpmData} />
      </div>

      {/* Health Summary */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6">
        <h3 className="text-lg font-semibold mb-3">Health Summary</h3>
        <ul className="space-y-2 text-gray-700">
          <li>{heartRisk}</li>
          <li>{feverRisk}</li>
          <li>{oxygenStatus}</li>
          <li>{respirationStatus}</li>
          <li>{skinTempStatus}</li>
          <li>{sweatStatus}</li>
        </ul>
      </div>

      {/* Bottom Navigation */}
      <div className="mt-auto bg-gradient-to-r from-blue-400 to-purple-500 shadow-inner p-2 flex justify-around items-center rounded-t-2xl">
        <button onClick={() => router.push("/dashboard")} className="p-2 text-white"><FaHome size={22} /></button>
        <button onClick={() => router.push("/dashboard/history")} className="p-2 text-white"><FaHistory size={22} /></button>
        <button onClick={() => router.push("/dashboard/appointments")} className="p-2 text-white"><FaCalendarAlt size={22} /></button>
        <button onClick={() => router.push("/dashboard/profile")} className="p-2 text-white"><FaUser size={22} /></button>
      </div>
    </div>
  );
}
